@import url('https://fonts.googleapis.com/css2?family=Roboto');

html,
body,
.App {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  color: #7f7f7f;
  font-family: 'Roboto', sans-serif;
}
.App {
  text-align: center;
  align-items: center;
}

div {
  display: flex;
}

.day-data {
  flex-direction: column;
  background: pink;
  margin: 2px;
  padding: 5px;
}

.time-data {
  flex-direction: row;
  justify-content: space-evenly;
}

.day-times {
  flex-direction: column;
  padding: 8px;
}

.nowind {
  background: #bee2be;
}

.noswell {
  background: #e0ffe0;
}

.nowind.noswell {
  background: turquoise;
}

@media (max-width: 1920px) {
  .App {
    font-size: 30px;
  }
  .day-data {
    width:450px;
    height:120px;
  }
  .day-times { 
    font-size:16px;
  }
}

@media (max-width: 1000px) {
  .App {
    font-size: 25px;
  }
  .day-data {
    width:350px;
    height:120px;
  }
  .day-times { 
    font-size:16px;
  }
}

@media (max-width: 480px) {
  .App {
    font-size: 16px;
  }
  .day-data {
    width:100%;
    height:120px;
  }
  .day-times { 
    font-size:14px;
  }
}
